<template lang="html">
  <div class="main receptacle post-view">
    <article class="entry">
      <header>
        <h1 class="multiline1 entry-title">{{title}}</h1>
        <div class="entry-meta">
          <a>
            <z-imageinput
            :width=36
            :height=36
            :src="'static/images/avatar.jpg'"
            :alt="''"
            :radius="'50%'"></z-imageinput>{{author.name}}
          </a>
          <span class="bull"></span>
          <time>{{publishedTime}}</time>
        </div>
      </header>

      <section class="entry-content" v-html='content'>
        <!-- <p></p>
        <p></p>
        <p></p>
        <p></p>
        <p></p>
        <p></p>
        <p></p>
        <p></p>
        <p></p>
        <p></p>
        <p></p>
        <p></p> -->
      </section>
    </article>
  </div>
</template>

<script>
import { zImageinput } from 'z-vue-components'

export default {
  data() {
    return {
      title: '',
      author: {
        name: ''
      },
      publishedTime: '',
      content: ''
    };
  },
  mounted() {
    this.getPost()
  },
  methods: {
    getPost: function() {
      this.$http.get('static/api/post.json').then((response) => {
        // success callback
        let data = response.data.data

        this.title = data.title
        this.author.name = data.author.name
        this.publishedTime = data.publishedTime
        this.content = data.content
      }, (response) => {
        // error callback
      });
    }
  },
  components: {
    zImageinput
  }
};
</script>

<style lang="less" scoped>
.post-view {
  margin-top: 0;
  padding-top: 22px;
  padding-bottom: 0;

  .entry-title {
    font-size: 32px;
    line-height: 45px;
    font-weight: 700;
  }

  .entry-meta {
    margin: 22px 0 0;
    height: 36px;
    line-height: 36px;
    color: gray;
    font-size: 14px;

    .z-image-wraper {
      margin-right: 12px;
      margin-top: -4px;
      vertical-align: middle;
    }

    .bull {
      margin: 0 3px;
    }
  }

  .entry-content {
    margin: 30px 0;
    line-height: 1.7;
    font-weight: 400;

    p+p {
      margin: 20px 0;
    }
  }
}

@media screen and (max-width: 660px) {
  .post-view {
    .entry {
      margin-top: -18px;
    }

    header, .entry-content {
      padding-left: 16px;
      padding-right: 16px;
    }
  }
}
</style>



